@import "~bulma/css/bulma.css";

/* ------------------- Defaults ------------------- */
html,
body,
#root, .App {
    height: 100%;
    font-family: 'Spectral SC', serif;
    color: #000;
}
.layout-boxed html,
.layout-boxed body,
.layout-boxed #root,
.layout-boxed .App {
  height: 100%;
  font-family: 'Spectral SC', serif;
  color: #000;
}

/* -------------------- Resets --------------------- */
body, form, fieldset, ol, ul, li, h1, h2, h3, h4, h5, h6, p {
    margin:0;
    padding:0;
    }
img {
    border:0; /* kills Gecko bug when img's are placed inside links */
    vertical-align:bottom; /* set vertical align to bottom for IE */
}

/* ------------------- Links ---------------------- */
a {
    text-decoration:none;
    color:#000;
    outline:0; /* removes dotted border */
}
a:visited {
color:#fff;
}
a:hover, a:focus, a:active {
text-decoration:none;
/* add-custom:outline-rule-here; */
}

/* -------------------- Forms ---------------------- */
fieldset {
    border:none;
}
form :focus {
    outline:0; /* removes Safari's blue border */
}  
input, select, textarea {
    font-size:1em;
    font-family:Arial, Helvetica, sans-serif;
}
label {
    cursor:pointer;
    vertical-align:middle;
}
input {
    vertical-align:middle;
}
textarea { 
    overflow:auto; /* removes scrollbar from IE when none is needed */
}

/* ------------------ Typography ------------------- */
h1, h2, h3, h4, h5, h6, p {
    margin: 0;
    }
    p, ul, ol {
    font-size:1em;
    }
    h1 {font-size:1.6em;}
    h2 {font-size:1.5em;}
    h3 {font-size:1.4em;}
    h4 {font-size:1.3em;}
    h5 {font-size:1.2em;}
    h6 {font-size:1.1em;}

/* --------------------- Global ---------------------- */
.content-header{
    position: relative;
    text-align: center;
    .breadcrumb{
        position: absolute;
        top: 0;
        left: 0;
        a{
            color: #828282;
        }
    }
}



